@import './../../common/styles/sass/variables';

.contactsBlock {
    padding: 3.12rem 0;
    background-color: $bgColor;
    .contactsContainer {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        @media screen and (max-width: 624px) {
            justify-content: center;
        }
        .contactsInfo {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
            margin-bottom: 1.5rem;
            img {
                height: 2.5rem;
            }
        }
        .contactsForm {
            min-width: 50%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 1rem;
            @media screen and (max-width: 624px) {
                width: 70%;
                align-items: center;
            }
            @media screen and (max-width: 550px) {
                width: 100%;
            }
            input, textarea {
                width: 100%;
                color: $titleColor;
                border: 1px solid rgba(11,11,19,.3);
                font-size: 0.875rem;
                height: calc(2em + 0.75rem + 2px);
                font-weight: 400;
                line-height: 1.5;
                padding: 0.375rem 0.75rem;
                background-color: $bgSecondColor;
            }
            textarea {
                min-height: calc(5.5em + 0.75rem + 2px);
            }
            .contactsButton {
                background: $titleColor;
                color: $bgColor;
                border: 1px solid $titleColor;
                border-radius: 4px;
                cursor: pointer;
                display: inline-block;
                font-size: 1rem;
                font-weight: 500;
                letter-spacing: .3px;
                line-height: 42px;
                padding: 0 25px;
                transition: .4s;
                &:hover {
                    background: $bgSecondColor;
                    color: $titleColor;
                }
            }
        }
    }
}